.home {
  height: calc(100vh - 50px);
  background-color: lighten(#aaa, 30%);
}

.home__content {
  text-align: center;
  max-width: 650px;
  margin: 0 auto;
}

.home__title {
  font-size: 36px;
  font-weight: 600;
  margin-bottom: 15px;
}

.home__text {
  font-size: 26px;
  font-weight: 400;
}

.home__descr {
  font-size: 20px;
  margin-top: 25px;

  button {
    margin: 0 10px;
  }
}
