.active-todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 500px;
  padding: 20px 30px;
  margin: 15px 0;
  border-radius: 10px;
  background-color: lighten(#ff4c4c, 28%);
  width: 100%;
  position: relative;
}

.active-todo__content {
  font-size: 22px;
  font-weight: 300;
  background: transparent;
  max-width: 85%;
  overflow-wrap: break-word;
}

.active-todo__actions {
  svg {
    font-size: 18px;
    transition: all 0.35s;
    cursor: pointer;
    margin-left: 15px;

    &:hover {
      opacity: 0.75;
    }
  }
}

.active-todo__icon-wrapper {
  position: absolute;
  top: -5px;
  left: -65px;

  width: 45px;
  height: 45px;
  border: 1px solid #aaa;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;

  color: #ff4c4c;

  svg {
    font-size: 18px;
    opacity: 0.5;
    transition: all 0.35s;
  }

  &:hover {
    svg {
      opacity: 0.9;
    }
  }
}

.active-todo__date {
  position: absolute;
  bottom: 1px;
  right: 5px;
  font-size: 15px;
  color: #aaa;
}

@media screen and (max-width: 650px) {
  .active-todo__icon-wrapper {
    position: absolute;
    top: -5px;
    left: -45px;

    width: 40px;
    height: 40px;

    svg {
      font-size: 15px;
    }
  }
}
