.sidebar {
  position: absolute;
  z-index: 100;
  width: 275px;
  height: calc(100vh - 50px);
  background-color: #ff4c4c;
  transform: translateX(-100%);
  transition: all 0.45s ease-in-out;

  &.active {
    transform: translateX(0);
  }
}

.sidebar__link {
  text-align: left;
  display: block;
  margin: 15px;
  font-size: 22px;
  font-weight: 300;
  color: #fff;
  svg {
    margin-right: 10px;
    font-size: 22px;
  }

  span {
    display: inline-block;
    width: 20px;
    height: 20px;
    font-size: 18px;
    font-weight: 500;
    border-radius: 50%;
    background-color: #fff;
    color: rgba(#ff4c4c, 0.9);
    text-align: center;
  }

  &:hover {
    text-decoration: underline;
  }
}

.sidebar__link--active {
  text-decoration: underline;
  opacity: 0.75;
}

@media screen and (max-width: 900px) {
  .sidebar {
    width: 250px;
  }
}

@media screen and (max-width: 500px) {
  .sidebar {
    width: 200px;
  }
}
