.header {
  background-color: #ff4c4c;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 50px;
  padding: 0 15px;
}

.header__logo {
  font-size: 30px;
  font-weight: 500;
  color: #fff;
}

.header__icon {
  color: #fff;
  font-size: 20px;
  transition: all 0.35s;
  cursor: pointer;
  margin-right: 5px;

  &:hover {
    opacity: 0.75;
  }
}
