*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Kanit', sans-serif;
}

button {
  outline: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 1170px;
  width: 100%;

  margin: 0 auto;
  padding: 0 15px;
}

.small-container {
  max-width: 850px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.page {
  padding: 50px 0;
  height: calc(100vh -50px);
}

.btn {
  padding: 5px 20px;
  border-radius: 6px;
  border: none;
  font-size: 20px;
  transition: all 0.25s;
}

.btn-primary {
  background-color: #ff4c4c;
  border: 1px solid #ff4c4c;
  color: #fff;
  &:hover {
    color: #ff4c4c;
    background: transparent;
  }
}

.btn-secondary {
  background: transparent;
  color: #8d8c8c;

  &:hover {
    text-decoration: underline;
    color: darken(#8d8c8c, 15%);
  }
}

.default-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #8d8c8c;
  font-size: 26px;
  font-weight: 300;
}

.red {
  color: #a50101;
}

.green {
  color: #519834;
}

.orange {
  color: #f25a16;
}

.grey {
  color: #727272;
}
