.add-task {
  border: 1px solid #aaa;
  box-shadow: 0px 0px 2px 1px lighten(#aaa, 5%);
  border-radius: 10px;
  padding: 20px;
  position: relative;
  svg {
    cursor: pointer;
  }
}

.add-task__field {
  height: 50px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #aaa;
  outline: none;
  padding: 5px 10px;
  font-size: 20px;
  font-weight: 300;
}

.add-task__modal {
  position: absolute;
  top: 0;
  left: -35px;

  font-size: 18px;
  display: flex;
  flex-direction: column;

  svg {
    margin: 2.5px 0;
  }
}

.add-task__btns {
  padding-top: 20px;
  button {
    &:last-of-type {
      margin-left: 10px;
    }
  }
}
