.inbox {
  height: calc(100vh - 50px);
  background-color: lighten(#aaa, 30%);
}

.inbox__inner {
  display: flex;
  flex-direction: column;
}

.inbox__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 500;
}

.inbox__header-title {
  font-size: 30px;
}

.inbox__add {
  font-size: 25px;
  cursor: pointer;
  svg {
    font-size: 20px;
    color: #ff4c4c;
    margin-left: 10px;
    transition: all 0.15s ease-in-out;
  }

  &:hover {
    svg {
      transform: rotate(90deg);
      color: darken(#ff4c4c, 15%);
    }
  }
}

.inbox__modal {
  max-width: 500px;
  width: 100%;

  margin: 25px auto;
}

.inbox__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
