.completed-todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  margin: 15px auto;
  border-radius: 10px;
  background-color: lighten(#ff4c4c, 28%);
  max-width: 500px;
  width: 100%;
  position: relative;
}

.completed-todo__content {
  font-size: 22px;
  font-weight: 300;
  background: transparent;
  max-width: 85%;
  overflow-wrap: break-word;
}

.completed-todo__actions {
  svg {
    font-size: 18px;
    transition: all 0.35s;
    cursor: pointer;
    margin-left: 15px;
    &:hover {
      opacity: 0.75;
    }
  }
}

.completed-todo__date {
  position: absolute;
  font-size: 15px;
  color: #aaa;
  bottom: 1px;
  right: 5px;
}

.completed-todo__icon-wrapper {
  position: absolute;
  top: -5px;
  left: -65px;

  width: 45px;
  height: 45px;
  border: 1px solid #aaa;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;

  color: #ff4c4c;

  svg {
    font-size: 18px;
    opacity: 0.5;
    transition: all 0.35s;
  }

  &:hover {
    svg {
      opacity: 0.9;
    }
  }
}
